.Card {
    box-shadow: 0 4px 8px 0 #353b48;
    transition: 0.3s;
    width: 40%;
    margin-bottom: 2%;
}

.Card:hover {
    box-shadow: 0 4px 8px 0 #192a56;
    background: #353b48;
}

.Desc {
    padding: 2px 16px;
}

.Tags {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-content: space-between;
    color: #dcdde1;
}

@media screen and (max-width: 600px) {
    .Card {
        width: 100%;
    }
}
